import React, { Component } from 'react';
import './App.css';

class Gallery extends Component {
  constructor(props) {
    super(props);
    this.state = {
      playingUrl: '',
      audio: null,
      isPlaying: false
    };
  }

  playAudio(preview_url) {
    console.log('preview_url', preview_url);
    let audio = new Audio(preview_url);
    if (!this.state.isPlaying) {
      audio.play();
      this.setState({
        isPlaying: true,
        playingUrl: preview_url,
        audio
      });
    } else {
      if (this.state.playingUrl === preview_url) {
        this.state.audio.pause();
        this.setState({ isPlaying: false });
      } else {
        this.state.audio.pause();
        audio.play();
        this.setState({
          isPlaying: true,
          playingUrl: preview_url,
          audio
        });
      }
    }
  }

  render() {
    // console.log('gallery props', this.props);
    const { tracks } = this.props;
    if (tracks === null) return <div />;
    console.log('tracks', tracks);
    return (
      <div>
        {tracks.map((track, k) => {
          const trackImg = track.album.images[0].url;
          if (track.preview_url !== null)
            return (
              <div
                key={k}
                className="track"
                onClick={() => this.playAudio(track.preview_url)}
              >
                <img src={trackImg} className="track-img" alt="track" />
                <div className="track-play">
                  <div className="track-play-inner">
                    {this.state.playingUrl === track.preview_url &&
                    this.state.isPlaying === true ? (
                      <span>| |</span>
                    ) : (
                      <span>&#9654;</span>
                    )}
                  </div>
                </div>
                <p className="track-text">{track.name}</p>
              </div>
            );
          else {
            return <div>Not found</div>
          }            
        })}
      </div>
    );
  }
}

export default Gallery;



