import React, { Component } from 'react';
import './App.css';
import { FormGroup, FormControl, InputGroup, Glyphicon } from 'react-bootstrap';
import axios from 'axios';
import Profile from './Profile';
import Gallery from './Gallery';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      query: '',
      artist: null,
      tracks: null,
      access_token: ''
    };
  }

  getNewToken = () => {
    const url = window.location.href;
    let access_token = url.match(/#(?:access_token)=([\S\s]*)/);
    if (access_token != null) access_token = access_token[1];
    console.log('url', url, 'access_token', access_token);
    return access_token;
  };

  search() {
    const BASE_URL = 'https://api.spotify.com/v1/search?';
    let FETCH_URL = `${BASE_URL}q=${this.state.query}&type=artist&limit=1`;
    const ALBUM_URL = `https://api.spotify.com/v1/artists`;
    const USER_TOKEN = this.getNewToken();
    const AuthStr = `Bearer ${USER_TOKEN}`;
    axios
      .get(FETCH_URL, { headers: { Authorization: AuthStr } })
      .then(response => {
        const artist = response.data.artists.items[0];
        console.log('artist', artist);
        this.setState({ artist });
        FETCH_URL = `${ALBUM_URL}/${artist.id}/top-tracks?country=US`;
        axios
          .get(FETCH_URL, { headers: { Authorization: AuthStr } })
          .then(response => {
            console.log("artist's top tracks:", response.data.tracks);
            const { tracks } = response.data;
            this.setState({ tracks });
          })
          .catch(error => console.log('error', error));
      })
      .catch(error => console.log('error', error));
  }

  render() {
    return (
      <div className="App">
        <a href="/login" className="btn btn-primary">
          Log in with Spotify
        </a>
        <div className="App-title">Music master</div>
        <FormGroup>
          <InputGroup>
            <FormControl
              className="App-searchbox"
              type="text"
              placeholder="Nhập tên nghệ sĩ"
              query={this.state.query}
              onChange={event => {
                this.setState({ query: event.target.value });
              }}
              onKeyPress={event => {
                if (event.key === 'Enter') this.search();
              }}
            />
            <InputGroup.Addon onClick={() => this.search()}>
              <Glyphicon glyph="search" />
            </InputGroup.Addon>
          </InputGroup>
        </FormGroup>
        {this.state.artist !== null ? (
          <div>
            <Profile artist={this.state.artist} />
            <Gallery tracks={this.state.tracks} />
          </div>
        ) : (
          <div />
        )}
      </div>
    );
  }
}

export default App;



